import { NextResponse } from 'next/server'
import { createClient } from '@supabase/supabase-js'

type TestUser = {
  email: string
  password: string
  profile: {
    display_name: string
    title: string
    superpower: string
    ask: string
    linkedin?: string
    profile_image?: string
    wants_meet: boolean
  }
}

export async function POST() {
  const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL
  const serviceKey = process.env.SUPABASE_SERVICE_ROLE_KEY

  if (!supabaseUrl || !serviceKey) {
    return NextResponse.json(
      { error: 'Server is missing Supabase env vars. Ensure NEXT_PUBLIC_SUPABASE_URL and SUPABASE_SERVICE_ROLE_KEY are set.' },
      { status: 500 }
    )
  }

  const supabase = createClient(supabaseUrl, serviceKey)

  const testUsers: TestUser[] = [
    {
      email: 'alice@example.com',
      password: 'password123',
      profile: {
        display_name: 'Alice Johnson',
        title: 'Software Engineer',
        superpower:
          'Building scalable web applications with React and Node.js. I love creating intuitive user experiences and solving complex technical challenges.',
        ask: 'Looking to connect with product managers and designers who are passionate about user-centered design and rapid prototyping.',
        linkedin: 'https://linkedin.com/in/alice-johnson',
        profile_image:
          'https://images.unsplash.com/photo-1494790108755-2616b612b786?w=150&h=150&fit=crop&crop=face',
        wants_meet: true,
      },
    },
    {
      email: 'bob@example.com',
      password: 'password123',
      profile: {
        display_name: 'Bob Smith',
        title: 'Product Manager',
        superpower:
          'Translating complex business requirements into clear product roadmaps. I excel at stakeholder management and data-driven decision making.',
        ask: 'Seeking collaboration with engineers and designers to build products that truly solve user problems.',
        linkedin: 'https://linkedin.com/in/bob-smith',
        profile_image:
          'https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?w=150&h=150&fit=crop&crop=face',
        wants_meet: false,
      },
    },
    {
      email: 'carol@example.com',
      password: 'password123',
      profile: {
        display_name: 'Carol Davis',
        title: 'UX Designer',
        superpower:
          'Creating beautiful, accessible, and user-friendly interfaces. I specialize in user research and design systems.',
        ask: 'Want to work with developers and PMs who prioritize user experience and accessibility in their products.',
        linkedin: 'https://linkedin.com/in/carol-davis',
        profile_image:
          'https://images.unsplash.com/photo-1438761681033-6461ffad8d80?w=150&h=150&fit=crop&crop=face',
        wants_meet: true,
      },
    },
    // Prominent profiles
    {
      email: 'satya.nadella@microsoft.com',
      password: 'password123',
      profile: {
        display_name: 'Satya Nadella',
        title: 'Chairman & CEO, Microsoft',
        superpower:
          'Driving transformative innovation through a deep focus on cloud computing, AI, and collaboration—reshaping Microsoft into a platform of empowerment.',
        ask: 'Looking to partner with visionary leaders who believe in the power of technology to unlock human potential and drive inclusive progress.',
        linkedin: 'https://linkedin.com/in/satya-nadella',
        profile_image: 'https://upload.wikimedia.org/wikipedia/commons/e/e1/Satya_Nadella_2020.jpg',
        wants_meet: true,
      },
    },
    {
      email: 'jensen.huang@nvidia.com',
      password: 'password123',
      profile: {
        display_name: 'Jensen Huang',
        title: 'CEO & Founder, Nvidia',
        superpower:
          'Forging the semiconductor revolution by steering Nvidia from gaming GPUs to AI and generative breakthroughs—making it one of the most influential tech companies globally.',
        ask: 'Seeking collaborations with innovators exploring the next frontier in AI acceleration and computational breakthroughs.',
        linkedin: 'https://linkedin.com/in/jensen-huang',
        profile_image: 'https://upload.wikimedia.org/wikipedia/commons/6/6e/Jensen_Huang_2016.jpg',
        wants_meet: true,
      },
    },
    {
      email: 'sam.altman@openai.com',
      password: 'password123',
      profile: {
        display_name: 'Sam Altman',
        title: 'CEO, OpenAI',
        superpower:
          'Leading AI development responsibly and purposefully—bridging cutting-edge research with real-world impact.',
        ask: 'Eager to team up with mission-driven thinkers and organizations that aim to harness AI for societal good.',
        linkedin: 'https://linkedin.com/in/sam-altman',
        profile_image: 'https://upload.wikimedia.org/wikipedia/commons/f/f2/Sam_Altman_2023.jpg',
        wants_meet: true,
      },
    },
    {
      email: 'alex.karp@palantir.com',
      password: 'password123',
      profile: {
        display_name: 'Alex Karp',
        title: 'CEO, Palantir Technologies',
        superpower:
          "Combining philosophical nuance and data-driven strategy to helm Palantir's explosive growth, establishing it as an S&P 500 standout.",
        ask: 'Looking to explore dialogues with thinkers in ethical data use, national security, and AI governance.',
        linkedin: 'https://linkedin.com/in/alex-karp',
        profile_image: 'https://upload.wikimedia.org/wikipedia/commons/2/25/Alex_Karp_2019.jpg',
        wants_meet: false,
      },
    },
    {
      email: 'peter.wang@anaconda.com',
      password: 'password123',
      profile: {
        display_name: 'Peter Wang',
        title: 'Co-Founder & Chief AI & Innovation Officer, Anaconda',
        superpower:
          'Empowering millions through open-source data science tools and leading innovation in Python-based analytics for data scientists worldwide.',
        ask: 'Interested in collaborating on projects that enable inclusive, scalable tools for data science communities.',
        linkedin: 'https://linkedin.com/in/peter-wang',
        profile_image: 'https://www.anaconda.com/hubfs/team/PeterWang.jpg',
        wants_meet: true,
      },
    },
    {
      email: 'eric.weber@yelp.com',
      password: 'password123',
      profile: {
        display_name: 'Eric Weber',
        title: 'Head of Experimentation, Yelp',
        superpower:
          'Architecting experimentation and blending business insight with scientific rigor—excelling in A/B testing and data-driven decision-making.',
        ask: 'Looking to connect with data science teams and product leaders interested in rigorous experimentation and growth optimization.',
        linkedin: 'https://linkedin.com/in/eric-weber',
        profile_image:
          'https://media.licdn.com/dms/image/C5603AQEqFfQ8Idas3g/profile-displayphoto-shrink_800_800/0/1517691031479?e=2147483647&v=beta&t=zzNm7PjAg4lJoO-Z2FnnnqvQPYx2gQ42oZVvGnVm-MI',
        wants_meet: false,
      },
    },
  ]

  const results: Array<Record<string, unknown>> = []

  for (const user of testUsers) {
    try {
      const { data: authData, error: authError } = await supabase.auth.admin.createUser({
        email: user.email,
        password: user.password,
        email_confirm: true,
      })

      if (authError) {
        results.push({ email: user.email, success: false, error: authError.message })
        continue
      }

      if (!authData.user) {
        results.push({ email: user.email, success: false, error: 'No user returned' })
        continue
      }

      const { error: profileError } = await supabase
        .from('profiles')
        .update(user.profile)
        .eq('id', authData.user.id)

      if (profileError) {
        results.push({ email: user.email, success: false, error: profileError.message })
        continue
      }

      results.push({ email: user.email, success: true, userId: authData.user.id })
    } catch (e: any) {
      results.push({ email: user.email, success: false, error: e?.message || 'Unexpected error' })
    }
  }

  return NextResponse.json({ results })
}


